body {
  -webkit-user-select: none;
  user-select: none;
  background: #000;
  margin: 0;
  overflow: hidden;
}

canvas {
  z-index: -1;
  display: block;
}

.title {
  color: #fff;
  mix-blend-mode: normal;
  font-family: Arial;
  font-size: .8rem;
  position: absolute;
}

.title._1 {
  top: 20px;
  left: 30px;
}

.title._2 {
  bottom: 20px;
  left: 30px;
}

.title._3 {
  cursor: pointer;
  bottom: 20px;
  right: 30px;
}

.terminal-window {
  text-align: left;
  border-radius: 10px;
  height: 360px;
  margin: auto;
  position: relative;
}

.terminal-window header {
  background: #e0e8f0;
  border-radius: 8px 8px 0 0;
  height: 30px;
  padding-left: 10px;
}

.terminal-window header .button {
  border-radius: 8px;
  width: 12px;
  height: 12px;
  margin: 10px 4px 0 0;
  display: inline-block;
}

.terminal-window header .button.green {
  background: #3bb662;
}

.terminal-window header .button.yellow {
  background: #e5c30f;
}

.terminal-window header .button.red {
  background: #e75448;
}

.terminal-window section.terminal {
  color: #fff;
  box-sizing: border-box;
  background: #30353a;
  width: 100%;
  padding: 10px;
  font-family: Menlo, Monaco, Consolas, Courier New, Courier;
  font-size: 11pt;
  position: absolute;
  top: 30px;
  bottom: 0;
  overflow: auto;
}

.terminal-window section.terminal .typed-cursor {
  opacity: 1;
  animation: .7s infinite blink;
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.terminal-data {
  display: none;
}

.terminal-window .gray {
  color: gray;
}

.terminal-window .green {
  color: green;
}
